export default {
    props: {
        label: {
            type: String,
            default: '标签'
        },
        value: {
            type: String,
            default: '请选择'
        },
        checked: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            dataName: 'lksdjfk'
        }
    },
    methods: {
        change(val) {
            this.$emit('change', {
                label: this.label,
                checked: val.detail.value,
                value:this.value
            })
        }
    },
}
